<template>
  <div class="left-pad" @contextmenu.prevent="padMenu($event)" data-type="file">
    <el-tree
      :data="data"
      :node-key="data.id"
      default-expand-all
      :props="defaultProps"
      @node-contextmenu="onContextMenu"
      :expand-on-click-node="true"
      :render-content="renderContent"
    ></el-tree>

    <costomMenu @addStore="addStore" ref="costomMenu" />
    <!-- 添加冰箱 -->
    <bingxiangM />
    <!-- 添加液氮罐 -->
    <yeDanguanM />
    <!-- 添加冻存架 -->
    <dongcunjiaM />
  </div>
</template>

<script>
import costomMenu from '../components/contextmenu'
import bingxiangM from '../components/addBinxnagModel'
import yeDanguanM from '../components/addYeDanGuanModel'
import dongcunjiaM from '../components/addDongcunjiaModel'

export default {
  props: [],
  data () {
    return {
      data: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  components: { costomMenu, bingxiangM, yeDanguanM, dongcunjiaM },

  computed: {},

  watch: {},

  methods: {
    // 获取列表
    getList () {
      var self = this
      this.$axios.get('http://localhost:8080/test.json').then(res => {
        var data = res.data.store
        this.data = this.setTreeData(data)
      })
    },
    // 设置树形数据
    setTreeData (source) {
      const cloneData = JSON.parse(JSON.stringify(source)) // 对源数据深度克隆
      return cloneData.filter(father => {
        const branchArr = cloneData.filter(child => father.id == child.parentId) // 返回每一项的子级数组
        branchArr.length > 0 ? father.children = branchArr : '' // 如果存在子级，则给父级添加一个children属性，并赋值
        return father.parentId == 0 // 返回第一层
      })
    },
    // 返回自定义 icon
    renderContent (h, { node, data, store }) {
      return (
        <span class="custom-tree-node">
          <img class="file-img" src={data.type} />
          <span data-type={data.type} class="file">{node.label}</span>
        </span>)
    },
    // 右击结构菜单
    onContextMenu (event, data, node) {
      console.log(data)
      this.$nextTick(() => {
        this.$refs.costomMenu.onContextMenu(event, data)
      })
    },
    // 右击面板结构菜单
    padMenu (event) {
      this.$refs.costomMenu.onContextMenu(event, this.data[0])
    },
    addStore (item) {
      console.log(item)
      switch (item.type_id) {
        case 1:
          // 添加冰箱
          this.$store.commit('modalShow', 'bingxiangModel')
          break
        case 2:
          // 添加液氮罐
          this.$store.commit('modalShow', 'yedanguanModel')
          break
        case 8:
          // 添加冻存架
          this.$store.commit('modalShow', 'dongcunjiaModel')
          break
        case 3:
          // 添加存储空间
          break
        case 4:
          // 修改
          break
        case 5:
          // 复制
          break
        case 6:
          // 剪切
          break
        case 7:
          // 删除
          break
      }
    }
  },

  created () {
    this.getList()
  },

  mounted () { }
}
</script>

