  padding-top: 10px;
}
.file-img {
  width: 13px;
  height: 20px;
  margin-right: 5px;
  margin-top: -2px;
}
.el-tree-node__expand-icon.is-leaf {
  color: #333;
  cursor: default;
}
</style>
